.galeria{
    padding:$separacion 0;
    
    h3{
        text-align: center;
    }
}

.galeria-imagenes{
    padding: 0;
    list-style: none;
    gap: 2rem 5rem;
    justify-content: center;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(20rem, auto));
    @include desktop{
        @include grid(6, $separacion)
    }
}

.overlay{
    position: fixed;
    background-color: rgba($color: #000000, $alpha: 0.5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
    
    .btn-cerrar{
        background-color: rgba(255, 255, 255, 0.3);
        width:5rem ;
        height: 5rem;
        border-radius: 50%;
        display: grid;
        place-items: center;
        font-size: 3rem;
        cursor: pointer;


    }

}