.boletos{
    padding: $separacion 0;
    h3{
        text-align: center;
        color: $blanco;
    }

    .contenedor-boletos{
        margin-top: $separacion / 2;
        display:flex;
        flex-direction: column;
        align-items: center;

        @include tablet{
        @include grid(2, $separacion);
        
        }
    }


    .plan{

        ul{
            list-style: none;
            padding-left: 0;
            
        }
        
        p{
            color: $blanco;
            font-size: 3rem;
            text-align: center;
            margin-bottom: 2rem;

        }
        .incluye{
            background-color: $rosa;
            text-align: center;
            font-size: 3rem;
            color: $blanco;
            padding: 0.5rem;
        }

        .pase-elemento{
            background-color: $blanco;
            padding: 1rem;
            text-align: center;
        }
        
    }

}


.boletos{
    .plan{
        .valor{
            background-color: $morado;
            text-align: center;
            font-size: 3rem;
            padding: 1rem 0;
        }
    }
}

